<template>
  <div class="add-model-factor">
    <Form
      ref="formValidate"
      :model="formValidate"
      :rules="ruleValidate"
      :label-width="120"
      class="form-content"
    >
      <FormItem label="枚举项目" prop="enums">
        <Input v-model="formValidate.enums" placeholder="请输入枚举项目"></Input>
      </FormItem>
      <FormItem label="值下限" prop="minValue">
        <Input v-model="formValidate.minValue" placeholder="请输入值下限"></Input>
      </FormItem>
      <FormItem label="值上限" prop="maxValue">
        <Input v-model="formValidate.maxValue" placeholder="请输入值上限"></Input>
      </FormItem>
      <FormItem label="对应分值" prop="score">
        <Input v-model="formValidate.score" placeholder="请输入对应分值"></Input>
      </FormItem>
      <FormItem label="定性得分" prop="qualScore">
        <Input v-model="formValidate.qualScore" placeholder="请输入定性得分"></Input>
      </FormItem>
      <FormItem label="状态">
        <RadioGroup v-model="formValidate.flag">
          <Radio label="0">停用</Radio>
          <Radio label="1">启用</Radio>
      </RadioGroup>
      </FormItem>
      <FormItem label="备注" prop="remark">
        <Input
          v-model="formValidate.remark"
          type="textarea"
          :autosize="{minRows: 2,maxRows: 5}"
          placeholder="请输入业务描述"
        ></Input>
      </FormItem>
      <FormItem >
        <div class="button-content">
          <Button type="primary" @click="handleSubmit('formValidate')">保存</Button>
          <Button @click="handleCancel">取消</Button>
        </div>
      </FormItem>
    </Form>
  </div>
</template>
<script>
export default {
  props: ["dataObj"],
  created(){
    if (this.dataObj.typeNm == "固有"){
            this.saveUrl = "/model/orgEvalModel/saveIndOption";
          if (this.dataObj.data){
              this.formValidate = this.dataObj.data;
              this.formValidate.id = this.dataObj.data.optId;
              delete this.formValidate.optId;
          }else {
              this.formValidate.indicatorId = this.dataObj.indicatorId;
          }
      }else {
          this.saveUrl = "/model/orgContrModel/saveContrIndOption";
          if (this.dataObj.data){
              this.formValidate = this.dataObj.data;
              this.formValidate.id = this.dataObj.data.optId;
              delete this.formValidate.optId;
          }else {
              this.formValidate.indicatorId = this.dataObj.indicatorId;
          }
      }
  },
  data() {
    const validateMinValue = (rule, value, callback) => {
      let regu = /^[+]{0,1}(\d+)$|^[+]{0,1}(\d+\.\d+)$/;
      let maxValue = this.formValidate.maxValue;
      if (!value){
          callback();
          return;
      }
      if (!regu.exec(value)) {
        callback(new Error("下限分值请输入非负数"));
        return;
      }
      if (regu.exec(maxValue) && parseFloat(maxValue) < parseFloat(value)){
        callback("下限分值不能大于上限分值");
        return;
      } 
      if (parseFloat(value) > 999.99){
          callback("下限分值不能大于999.99");
      }else {
        callback();
      }
    };
    const validateMaxValue = (rule, value, callback) => {
      let regu = /^[+]{0,1}(\d+)$|^[+]{0,1}(\d+\.\d+)$/;
      let minValue = this.formValidate.minValue;
      if (!value){
          callback();
          return;
      }
      if (!regu.exec(value)) {
        callback(new Error("上限分值请输入非负数"));
        return;
      }
      if (regu.exec(minValue) && parseFloat(minValue) > parseFloat(value)){
        callback("下限分值不能大于上限分值");
        return;
      } 
      if (parseFloat(value) > 999.99){
          callback("上限分值不能大于999.99");
      }else {
        callback();
      }
    };
    const validateScore = (rule, value, callback) => {
      let regu = /^[+]{0,1}(\d+)$|^[+]{0,1}(\d+\.\d+)$/;
      if (!value && value != 0){
          callback("对应分值不能为空");
          return;
      }
      if (!regu.exec(value)) {
        callback(new Error("对应分值请输入非负数"));
        return;
      }
      if (parseFloat(value) > 999.99){
          callback("对应分值不能大于999.99");
      }else {
        callback();
      }
    };
    const validateQualScore = (rule, value, callback) => {
      let regu = /^[+]{0,1}(\d+)$|^[+]{0,1}(\d+\.\d+)$/;
      if (!value){
          callback();
          return;
      }
      if (!regu.exec(value)) {
        callback(new Error("定性得分请输入非负数"));
        return;
      }
      if (parseFloat(value) > 999.99){
          callback("定性得分不能大于999.99");
      }else {
        callback();
      }
    };
    return {
      formValidate: {
        enums: "",
        minValue: "",
        maxValue: "",
        score: "",
        qualScore: "",
        flag: "1",
        remark: ""
      },
      saveUrl: "",
      ruleValidate: {
        enums: [
          {
            type: "string",
            max: 64,
            message: "枚举项目不能超过64个字！",
            trigger: "blur"
          }
        ],
        minValue: [
          {
            validator: validateMinValue,
            trigger: "blur"
          }
        ],
        maxValue: [
         {
            validator: validateMaxValue,
            trigger: "blur"
          }
        ],
        score: [
          {
            required: true,
            validator: validateScore,
            trigger: "blur"
          }
        ],
        qualScore: [
          {
            validator: validateQualScore,
            trigger: "blur"
          }
        ],
        remark: [
          {
            type: "string",
            max: 512,
            message: "备注不能超过512个字！",
            trigger: "blur"
          }
        ]
      }
    };
  },
  mounted() {
  },
  methods: {
    handleSubmit(name) {
      let data = JSON.stringify(this.formValidate);
      this.$refs[name].validate(valid => {
        if (valid) {
           this.axios
            .post(this.saveUrl, data, {
              headers: { "Content-Type": "application/json" }
            })
            .then(response => {
              if (response.code == "200") {
                this.$Message.success("保存成功");
                this.$emit("save");
              }
            })
            .catch(error => {});
        }
      });
    },
    handleCancel(name) {
      this.$emit("cancel", false);
    }
  }
};
</script>
<style lang="scss" scoped>
.add-model-factor {
  margin: 0 30px;
  label {
    font-size: 14px;
  }
  .form-content {
    .speical-span {
      width: 8.33333333%;
      float: left;
    }
    .ivu-date-picker {
      width: 100%;
    }
  }
}
</style>


