<template>
  <div class="ration-model-modify">
    <Form
      ref="formValidate"
      :model="formValidate"
      :rules="ruleValidate"
      :label-width="100"
      class="form-content"
    >
      <FormItem label="模型代码">
        <label>{{formValidate.modelCode}}</label>
      </FormItem>
      <FormItem label="模型名称" prop="modelName">
        <Input v-model="formValidate.modelName" placeholder="请输入模型名称"></Input>
      </FormItem>
      <FormItem label="模型版本">
        <label>{{formValidate.version}}</label>
      </FormItem>
      <FormItem label="适用范围">
        <Input
          v-model="formValidate.scope"
          type="textarea"
          :autosize="{minRows: 2,maxRows: 5}"
          placeholder="请输入备注"
        ></Input>
      </FormItem>
      <FormItem label="备注" prop="remark">
        <Input
          v-model="formValidate.remark"
          type="textarea"
          :autosize="{minRows: 2,maxRows: 5}"
          placeholder="请输入备注"
        ></Input>
      </FormItem>
      <FormItem >
        <div class="button-content">
          <Button type="primary" @click="handleSubmit('formValidate')">保存</Button>
          <Button @click="handleCancel">取消</Button>
        </div>
      </FormItem>
    </Form>
  </div>
</template>
<script>
export default {
  props: ["dataObj"],
  created() {
    this.formValidate = JSON.parse(JSON.stringify(this.dataObj.data));
    this.saveUrl = this.dataObj.typeNm == "固有"?"/model/orgEvalModel/saveOrgModel":"/model/orgContrModel/saveOrgContrModel";
  },
  data() {
    return {
      formValidate: {
        modelCode: "",
        modelName: "",
        version: "",
        scope: "",
        remark: ""
      },
      saveUrl: "",
      ruleValidate: {
        modelName: [
          {
            type: "string",
            max: 64,
            message: "模型名称不能超过64个字！",
            trigger: "blur"
          }
        ],
        scope: [
          {
            type: "string",
            max: 500,
            message: "适用范围不能超过500个字！",
            trigger: "blur"
          }
        ],
        remark: [
          {
            type: "string",
            max: 512,
            message: "备注不能超过512个字！",
            trigger: "blur"
          }
        ]
      }
    };
  },
  methods: {
    handleSubmit(name) {
      let data = JSON.stringify(this.formValidate);
      this.$refs[name].validate(valid => {
        if (valid) {
          this.axios
            .post(this.saveUrl, data, {
              headers: { "Content-Type": "application/json" }
            })
            .then(response => {
              if (response.code == "200") {
                this.$Message.success("保存成功");
                this.$emit("save");
              }
            })
            .catch(error => {});
        }
      });
    },
    updateTableBody2(item){
        this.id = item.id;
        this.axios
          .get(this.selectTable2Url+item.id)
          .then(response => {
            if (response.code == "200") {
                this.tableBody2 = response.data;           
            }
          })
          .catch(error => {});
    },
    handleCancel(name) {
      this.$emit("cancel", false);
    }
  }
};
</script>
<style lang="scss" @scoped>
.ration-model-modify {
  margin: 0 30px;
  label {
    font-size: 14px;
  }
  .form-content {
    .speical-span {
      width: 8.33333333%;
      float: left;
    }
    .ivu-date-picker {
      width: 100%;
    }
  }
}
</style>


