<template>
  <div class="tab2-detail">
    <div class="button-content1">
        <Button type="primary" @click="operate('1')" v-if="!noOpe">
            <i class="ivu-icon ivu-icon-md-add"></i>新增
        </Button>
        <!-- <Button type="primary" v-if="!noOpe">
            <i class="ivu-icon ivu-icon-md-arrow-up"></i>导出
        </Button> -->
    </div>
    <div class="contentCellBody">
      <Row>
        <table class="tableBox">
          <thead>
            <tr>
              <td v-for="(item,index) in tableHeader" v-if="index != 1 || !noOpe">{{item.name}}</td>
            </tr>
          </thead>
          <tbody>
            <template v-for="(item,index) in tableBody">
              <tr>
                <td>{{index+1}}</td>
                <td v-if="!noOpe">
                  <span class="spanBox" @click="operate('2', item)">修改</span>
                  <span class="spanBox" @click="operate('3', item)">删除</span>
                </td>
                <td>{{item.enums}}</td>
                <td>{{item.minValue}}</td>
                <td>{{item.maxValue}}</td>
                <td>{{item.score}}</td>
                <td>{{item.qualScore}}</td>
                <td>{{item.remark}}</td>
                <td>
                  <i-switch :value="item.flag" true-value="1" false-value="0" disabled>
                    <span slot="open">启用</span>
                    <span slot="close">停用</span>
                  </i-switch>
                </td>
              </tr>
            </template>
          </tbody>
        </table>
      </Row>
    </div>
    <Modal :mask-closable="false"  
      v-model="showAddModal"
      :title="modelTitle"
      @on-cancel="cancel"
      class="dialog"
      :styles="{width: '80%'}"
    >
      <component
        :is="tabview"
        @cancel="cancelDialog"
        @save="saveDatas"
        v-if="showAddModal"
        :dataObj="dataObjj"
        width="80%"
      ></component>
      <div slot="footer"></div>
    </Modal>
  </div>
</template>
<script>
import addModelFactor from './addModelFactor'
export default {
  props: ["dataObj"],
  created() {
      if (this.dataObj.typeNm == "固有"){
          if (this.dataObj.noOpe)
            this.url = `/model/orgEvalModel/getOptHisInfo/${this.dataObj.data.indicatorId}/${this.dataObj.data.modelVersion}`;
          else this.url = `/model/orgEvalModel/getIndOption/${this.dataObj.data.id}`;
      }else {
          if (this.dataObj.noOpe)
            this.url = `/model/orgContrModel/getContrOptHisInfo/${this.dataObj.data.indicatorId}/${this.dataObj.data.modelVersion}`;
          else this.url = `/model/orgContrModel/getContrIndOption/${this.dataObj.data.id}`;
      }
      this.noOpe = this.dataObj.noOpe;
      this.axios
      .get(this.url)
      .then(response => {
        if (response.code == "200") {
            this.tableBody = response.data;           
        }
      })
      .catch(error => {});  
  },
  data() {
    return {
      tabview: "",
      noOpe: false,
      url: "",
      modelTitle: "新增",
      showAddModal:false,
      dataObjj: {
        data: {}
      },
      tableHeader: [
        { name: "序" },
        { name: "操作" },
        { name: "枚举项目" },
        { name: "值下限" },
        { name: "值上限" },
        { name: "对应分值" },
        { name: "定性分值" },
        { name: "备注" },
        { name: "状态" }
      ],
      tableBody: [
        {
          id: "12",
          code: "111",
          name: "低",
          roleDescribe: "0",
          Remarks: "50"
        }
      ]
    };
  },
  components:{
    addModelFactor
  },
  methods: {
    saveDatas() {
      this.showAddModal = false;
      this.axios
      .get(this.url)
      .then(response => {
        if (response.code == "200") {
            this.tableBody = response.data;           
        }
      })
      .catch(error => {});  
    },
    cancelDialog(val) {
      this.showAddModal = val;
    },
    operate(type, item) {
      switch (type) {
        case "1":
          this.showAddModal = true;
          this.modelTitle = '新增';
          this.dataObjj = {
            typeNm: this.dataObj.typeNm,
            indicatorId: this.dataObj.data.id
          }
          this.tabview = 'addModelFactor';
          break;
        case "2":
          this.showAddModal = true;
          this.modelTitle = '修改';
          this.dataObjj = {
            typeNm: this.dataObj.typeNm,
            data: item
          }
          this.tabview = 'addModelFactor';
          break;
        case "3":
        let deleteUrl = this.dataObj.typeNm == "固有"?`/model/orgEvalModel/deleteOrgOption/${item.optId}`:`/model/orgContrModel/deleteContrOption/${item.optId}`;
        this.$Modal.confirm({
              title:'提示',
              content: "确认删除？删除后不可恢复！",
              okText: "确认",
              cancelText: "取消",
              onOk: () => {
                  this.axios
                  .get(deleteUrl)
                  .then(response => {
                    if (response.code == "200") {
                        this.$Message.success("操作成功");
                        this.axios
                        .get(this.url)
                        .then(response => {
                          if (response.code == "200") {
                              this.tableBody = response.data;           
                          }
                        })
                        .catch(error => {});             
                    }
                  })
                  .catch(error => {});  
              },
              onCancel: () => {
              }
          });
          break;
      }
    },
    cancel(){}
  }
};
</script>
<style scoped>
</style>


