<template>
  <div class="tab2-add">
    <Form
      ref="formValidate"
      :model="formValidate"
      :rules="ruleValidate"
      :label-width="150"
      class="form-content"
    >
      <FormItem label="级别" prop="indicatorLevel">
        <Input v-model="formValidate.indicatorLevel" placeholder="请输入级别"></Input>
      </FormItem>
      <FormItem label="编号" prop="indicatorCode">
        <Input v-model="formValidate.indicatorCode" placeholder="请输入编号" :disabled="!newItem"></Input>
      </FormItem>
      <!-- <FormItem label="指标项代码" prop="indCode">
        <Input v-model="formValidate.indCode" placeholder="请输入指标项代码" :disabled="!newItem"></Input>
      </FormItem> -->
      <FormItem label="指标项名称" prop="indicatorName">
        <Input v-model="formValidate.indicatorName" placeholder="请输入指标项名称"></Input>
      </FormItem>
      <FormItem label="有无子项">
        <RadioGroup v-model="formValidate.hasSon">
          <Radio label="1">有</Radio>
          <Radio label="0">无</Radio>
        </RadioGroup>
      </FormItem>
      <FormItem label="指标项类型">
        <RadioGroup v-model="formValidate.indicatorType">
          <Radio
            v-for="item in indicatorTypes"
            :label="item.code"
            :key="item.code"
          >{{item.name}}</Radio>
        </RadioGroup>
      </FormItem>
      <FormItem label="计算公式" prop="formula">
        <Input v-model="formValidate.formula" placeholder="请输入计算公式"></Input>
      </FormItem>
      <FormItem label="权重（%）" prop="weight">
        <Input v-model="formValidate.weight" placeholder="请输入权重（%）"></Input>
      </FormItem>
      <FormItem label="预警值" prop="earlyWarningValue">
        <Input v-model="formValidate.earlyWarningValue" placeholder="请输入预警值"></Input>
      </FormItem>
      <FormItem label="缺省值" prop="defaultValue">
        <Input v-model="formValidate.defaultValue" placeholder="请输入缺省值"></Input>
      </FormItem>
      <FormItem label="状态">
        <RadioGroup v-model="formValidate.flag">
          <Radio label="0">停用</Radio>
          <Radio label="1">启用</Radio>
        </RadioGroup>
      </FormItem>
      <FormItem label="确认指标内容是否在评估页面修改" v-if="typeNm=='固有'">
        <RadioGroup v-model="formValidate.isModify">
          <Radio label="0">否</Radio>
          <Radio label="1">是</Radio>
        </RadioGroup>
      </FormItem>
      <FormItem label="备注" prop="remark">
        <Input
          v-model="formValidate.remark"
          type="textarea"
          :autosize="{minRows: 2,maxRows: 5}"
          placeholder="请输入业务描述"
        ></Input>
      </FormItem>
      <FormItem label="指标项描述" prop="indicatorDesc" v-if="typeNm!='固有'">
        <Input
          v-model="formValidate.indicatorDesc"
          type="textarea"
          :autosize="{minRows: 2,maxRows: 5}"
          placeholder="请输入指标项描述"
        ></Input>
      </FormItem>
      <FormItem label="指标项内容" prop="indicatorContent" v-if="typeNm!='固有'">
        <Input
          v-model="formValidate.indicatorContent"
          type="textarea"
          :autosize="{minRows: 2,maxRows: 5}"
          placeholder="请输入指标项内容"
        ></Input>
      </FormItem>
      <FormItem >
        <div class="button-content">
          <Button type="primary" @click="handleSubmit('formValidate')">保存</Button>
          <Button @click="handleCancel">取消</Button>
        </div>
      </FormItem>
    </Form>
  </div>
</template>
<script>
export default {
  props: ["dataObj"],
  created() {
    if (this.dataObj.data) {
        this.formValidate = this.dataObj.data;
        this.newItem = false;
    }else {
      this.formValidate.modelId = this.dataObj.id;
    }
    this.typeNm = this.dataObj.typeNm;
    this.axios
      .get("/system/getDic/MODEL_IND_TYPE")
      .then(response => {
        if (response.code == "200") {
          this.indicatorTypes = response.data.MODEL_IND_TYPE;
        }
      })
      .catch(function(error) {});
  },
  data() {
    const validateIL = (rule, value, callback) => {
      let regu = /^[0-9]+?$/;
      if (!value){
          callback();
          return;
      }
      if (!regu.exec(value)) {
        callback(new Error("级别请输入非负整数"));
        return;
      }
      if (value.length > 11){
          callback("级别不能超过11个字");
      }else {
        callback();
      }
    };
     const validatorIndicatorCode = (rule, value, callback) =>{
      let url = this.typeNm == "固有"?`/model/orgEvalModel/isHaveOrgInd/${this.dataObj.id}/${value}`:`/model/orgContrModel/isHaveContrInd/${this.dataObj.id}/${value}`
      if (this.newItem) {
        this.axios
          .get(url)
          .then(response => {
            if (response.data == "false") {
              callback(new Error("指标项已存在"));
            } else {
              callback();
            }
          })
          .catch(error => {});
      } else {
        callback();
      }
    };
    return {
      formValidate: {
        indicatorLevel: "",
        indCode: "",
        indicatorCode: "",
        indicatorName: "",
        hasSon: "",
        indicatorType: "",
        formula: "",
        weight: "",
        earlyWarningValue:'',
        defaultValue: "",
        flag: "1",
        isModify: "0",
        remark: "",
        indicatorDesc: "",
        indicatorContent: ""
      },
      modelCode: "",
      typeNm: "",
      indicatorTypes: [],
      newItem: true,
      ruleValidate: {
        indicatorLevel: [
          {
            required: false,
            validator: validateIL,
            trigger: "blur"
          }
        ],
        indicatorCode: [
          {
            required: true,
            validator: (rule, value, callback) => {
                        let regu =/^[0-9\.]+$/;
                        if (!value && value != 0){
                            callback("编号不能为空");
                            return;
                        }
                        if (!regu.exec(value)) {
                          callback(new Error("编号只能输入数字或小数点"));
                          return;
                        }
                        if (value.length > 10){
                            callback("编号不能超过10个字");
                        }else {
                          callback();
                        }
            },
            trigger: "blur"
          },
          {
            validator: validatorIndicatorCode,
            trigger: "xxxx"
          }
        ],
        indCode: [
            {
            required: true,
            validator: (rule, value, callback) => {
                        if (!value && value != 0){
                            callback("指标项代码不能为空");
                            return;
                        }
                        if (value.length > 32){
                            callback("指标项代码不能超过32个字");
                        }else {
                          callback();
                        }
            },
            trigger: "blur"
          }
        ],
        indicatorName:[
          {
            type: "string",
            max: 64,
            message: "指标项名称不能超过64个字！",
            trigger: "blur"
          }
        ],
        formula:[
          {
            type: "string",
            max: 128,
            message: "计算公式不能超过128个字！",
            trigger: "blur"
          }
        ],
        weight:[
          {
            required: true,
            validator: (rule, value, callback) => {
                        let regu = /^[0-9]+?$/;
                        if (!value && value != 0){
                          callback("权重不能为空");
                          return;
                        }
                        if (!regu.exec(value)) {
                          callback(new Error("权重请输入非负整数"));
                          return;
                        }
                        if (Number(value) > 100){
                            callback("权重不能大于100");
                        }else {
                          callback();
                        }
            },
            trigger: "blur"
          }
        ],
        earlyWarningValue:[
          {
            validator: (rule, value, callback) => {
                        let regu = /^[0-9]+?$/;
                        if (!value){
                          callback();
                          return;
                        }
                        if (!regu.exec(value)) {
                          callback(new Error("预警值请输入非负整数"));
                          return;
                        }
                        if (value.length > 5){
                            callback("预警值长度不能超过5个字");
                        }else {
                          callback();
                        }
            },
          }
        ],
        defaultValue:[
            {
              validator: (rule, value, callback) => {
                        let regu = /^[0-9]+?$/;
                        if (!value){
                          callback();
                          return;
                        }
                        if (!regu.exec(value)) {
                          callback(new Error("缺省值请输入非负整数"));
                          return;
                        }
                        if (value.length > 5){
                            callback("缺省值长度不能超过5个字");
                        }else {
                          callback();
                        }
            },
          }
        ],
        remark: [
          {
            type: "string",
            max: 512,
            message: "备注不能超过512个字！",
            trigger: "blur"
          }
        ],
         indicatorDesc: [
          {
            type: "string",
            max: 512,
            message: "指标项描述不能超过512个字！",
            trigger: "blur"
          }
        ],
         indicatorContent: [
          {
            type: "string",
            max: 512,
            message: "指标项内容不能超过512个字！",
            trigger: "blur"
          }
        ]
      }
    };
  },
  methods: {
    handleSubmit(name) {
      let data = JSON.stringify(this.formValidate);
      let url = "";
      if (this.typeNm == "固有") {
        url = "/model/orgEvalModel/saveOrgIndicator";
      }else{
        url = "/model/orgContrModel/saveContrIndicator";
      }
      this.$refs[name].validate(valid => {
        if (valid) {
           this.axios
            .post(url, data, {
              headers: { "Content-Type": "application/json" }
            })
            .then(response => {
              if (response.code == "200") {
                this.$Message.success("保存成功");
                this.$emit("save");
              }
            })
            .catch(error => {});
        }
      });
    },
    handleCancel(name) {
      this.$emit("cancel", false);
    }
  }
};
</script>
<style lang="scss" scoped>
.tab2-add {
  margin: 0 30px;
  label {
    font-size: 14px;
  }
  .form-content {
    .speical-span {
      width: 8.33333333%;
      float: left;
    }
    .ivu-date-picker {
      width: 100%;
    }
  }
}
</style>


