<template>
  <div class="ration-model-set">
    <div class="topTitle">
      <span class="topTitle-span1">模型管理</span>
      <span class="topTitle-span1">
        <img src="../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">机构洗钱风险模型管理</span>
      <span class="topTitle-span1">
        <img src="../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">控制措施分析模型设置</span>
    </div>
    <div class="contentCellBox">
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>数据列表
          <div class="button-content">
            <Button type="primary" @click="operate('6')" v-authcontrol="{menuCode:'111003003001'}">
              <i class="ivu-icon ivu-icon-md-add"></i>新增模型指标项
            </Button>
            <!-- <Button type="primary">
              <i class="ivu-icon ivu-icon-md-arrow-down" v-authcontrol="{menuCode:'111003003001'}"></i>导入
            </Button>
            <Button type="primary" >
              <i class="ivu-icon ivu-icon-md-arrow-up" v-authcontrol="{menuCode:'111003003001'}"></i>导出
            </Button> -->
          </div>
        </div>
        <div class="contentCellBody">
          <Row>
            <table class="tableBox">
              <thead>
                <tr>
                  <td v-for="(item,index) in tableHeader">{{item.name}}</td>
                </tr>
              </thead>
              <tbody>
                <template v-for="(item,index) in tableBody">
                  <tr>
                    <td>{{index+1}}</td>
                    <td>{{item.modelCode}}</td>
                    <td>{{item.modelName}}</td>
                    <td>{{item.version}}</td>
                    <td>{{item.scope}}</td>
                    <td>
                        <i-switch v-model="item.flag" true-value="1" false-value="0" @on-change="changeUseSts(item,index)" v-authcontrol="{menuCode:'111003003006'}">
                            <span slot="open">启用</span>
                            <span slot="close">停用</span>
                        </i-switch>
                    </td>
                    <td>{{item.remark}}</td>
                    <td>{{item.updateTime}}</td>
                    <td>{{item.username}}</td>
                    <td>
                      <span class="spanBox" @click="operate('1', item)" v-authcontrol="{menuCode:'111003003004'}">修改</span>
                      <span class="spanBox" @click="operate('2', item)" v-authcontrol="{menuCode:'111003003007'}">发布版本</span>
                      <span class="spanBox" @click="operate('3', item)" v-authcontrol="{menuCode:'111003003005'}">历史版本</span>
                    </td>
                  </tr>
                </template>
              </tbody>
            </table>
            <table class="tableBox mt_20">
              <thead>
                <tr>
                  <td v-for="(item,index) in tableHeader2">{{item.name}}</td>
                </tr>
              </thead>
              <tbody>
                <template v-for="(item,index) in tableBody2">
                  <tr :key="index">
                    <td>{{item.indicatorCode}}</td>
                    <td>
                      <el-popover
                    placement="top-start"
                    title
                    width="200"
                    trigger="hover"
                    :content="item.indicatorName"
                  >
                    <p slot="reference" class="textOverflow textMaxWidth">{{item.indicatorName}}</p>
                  </el-popover>
                    </td>
                    <td>{{item.formula}}</td>
                    <td>{{item.weight}}</td>
                    <td>
                        <i-switch v-model="item.flag" true-value="1" false-value="0" @on-change="changeUseSts2(item, index)" >
                            <span slot="open">启用</span>
                            <span slot="close">停用</span>
                        </i-switch>
                    </td>
                    <td>{{item.updateTime}}</td>
                    <td>{{item.updateName}}</td>
                    <td>
                      <span class="spanBox" @click="operate('5', item)" v-if="item.hasSon == '0'">详情</span>
                      <span class="spanBox" @click="operate('7', item)" v-authcontrol="{menuCode:'111003003008'}">修改</span>
                    </td>
                  </tr>
                </template>
              </tbody>
            </table>
          </Row>
        </div>
      </div>
    </div>
    <Modal :mask-closable="false"  
      v-model="showAddModal"
      :title="modelTitle"
      @on-cancel="cancel"
      class="dialog"
      :styles="{width: '80%'}"
    >
      <component :is="tabview" @cancel="cancelDialog" @save="saveDatas" :dataObj="dataObj" width="80%" v-if="showAddModal"></component>
      <div slot="footer"></div>
    </Modal>
  </div>
</template>
<script>
//table1部分
import rationModelModifyOrga from "../common/rationModelSet/rationModelModifyOrga";//修改
import historyVersionOrga from "../common/rationModelSet/historyVersionOrga";//历史版本
//table2部分
import tab2Analyze from "../common/rationModelSet2/tab2Analyze";//分析
import tab2Detail from "../common/rationModelSet2/tab2Detail";//详情
import tab2Add from "../common/rationModelSet2/tab2Add";//新增 修改
export default {
  components: {
    historyVersionOrga,
    rationModelModifyOrga,
    tab2Analyze,
    tab2Detail,
    tab2Add,
  },
  created() {
    this.axios
    .get("/model/orgContrModel/getOrgContrModel")
    .then(response => {
      if (response.code == "200") {
          this.tableBody = [];
          this.tableBody.push(response.data.orgEvalContrModel);
          this.tableBody2 = response.data.orgEvalContrIndicator;
      }
    })
    .catch(error => {});
  },
  data() {
    return {
      tabview:'rationModelModifyOrga',
      showAddModal: false,
      dataObj:{
        data:{},
      },
      modelTitle: "新增",
      tableHeader: [
        { name: "序" },
        { name: "模型代码" },
        { name: "模型名称" },
        { name: "模型版本" },
        { name: "适用范围" },
        { name: "状态" },
        { name: "备注" },
        { name: "更新时间" },
        { name: "更新人员" },
        { name: "操作" },
      ],
      tableBody: [
      ],
      tableHeader2: [
        { name: "编号" },
        { name: "指标项" },
        { name: "计算公式" },
        { name: "权重（%）" },
        { name: "状态" },
        { name: "更新时间" },
        { name: "更新人员" },
        { name: "操作" },
      ],
      tableBody2: [
      ]
    };
  },
  methods:{
    /**
     * name：改变用户停用状态
     */
    changeUseSts(item, index){
        let url = "/model/orgContrModel/updateContrModelStatus/";
        this.axios
          .get(url+item.id)
          .then(response => {
            if (response.code == "200") {
                this.$Message.success("更新成功");
            }
          })
          .catch(error => {
              this.tableBody[index].flag = this.tableBody[index].flag == "0"?"1":"0";
              this.$Message.error("操作失败"); 
          });
    },
    changeUseSts2(item, index) {
        let url = "/model/orgContrModel/updateContrIndStatus/";
        this.axios
          .get(url+item.id+'/'+item.flag)
          .then(response => {
            if (response.code == "200") {
                this.$Message.success("更新成功");
                this.axios
                .get("/model/orgContrModel/getOrgContrModel")
                .then(response => {
                  if (response.code == "200") {
                      this.tableBody = [];
                      this.tableBody.push(response.data.orgEvalContrModel);
                      this.tableBody2 = response.data.orgEvalContrIndicator;
                  }
                })
                .catch(error => {});
            }
          })
          .catch(error => {
              this.tableBody2[index].flag = this.tableBody2[index].flag == "0"?"1":"0";
              this.$Message.error("操作失败"); 
          });
    },
    cancel() {},
    operate(type, item){
      switch (type) {
        case '1':
          this.modelTitle = '修改'
          this.tabview = 'rationModelModifyOrga';
          this.showAddModal = true;
          this.dataObj = {
            typeNm:"控制",
            data:item
          };
          break;
        case '2':
         if (item.flag != '1') {
              this.$Message.warning({
                content:"发布状态未启用的评级模型，无法生成模型版本！",
                duration: 2.5
                });
              return;
          }
          let url = `/model/orgContrModel/publishContrModel/${item.id}`;
          this.$Modal.confirm({
              title:'提示',
              content: "确定生成模型版本么？温馨提示：确保模型规则引擎为最新，最好重启发布状态？",
              okText: "确认",
              cancelText: "取消",
              onOk: () => {
                  this.axios
                  .get(url)
                  .then(response => {
                    if (response.code == "200") {
                        this.$Message.success("操作成功");
                        item.version = Number(item.version)+1;          
                    }
                  })
                  .catch(error => {});
              },
              onCancel: () => {
              }
          });
          break;
        case '3':
          this.modelTitle = '还原历史版本'
          this.tabview = 'historyVersionOrga';
          this.dataObj = {
            typeNm:"控制",
            data:item
          };
          this.showAddModal = true;
          break;
        case '4':
          this.modelTitle = '有效性分析'
          this.tabview = 'tab2Analyze';
          this.showAddModal = true;
          break;
        case '5':
          this.dataObj = {
            typeNm:"控制",
            data:item,
            noOpe: false
          };
          this.modelTitle = '模型因子'
          this.tabview = 'tab2Detail';
          this.showAddModal = true;
          break;
        case '6':
          this.modelTitle = '新增'
          this.tabview = 'tab2Add';
          this.showAddModal = true;
          this.dataObj = {
            typeNm:"控制",
            id: this.tableBody[0].id
          };
          break;
        case '7':
          this.modelTitle = '修改模型指标'
          this.tabview = 'tab2Add';
          this.showAddModal = true;
          this.dataObj = {
            typeNm:"控制",
            data:item,
          };
          break;
        case '8':
          this.$Modal.confirm({
              title:'提示',
              content: "是否删除？",
              okText: "确认",
              cancelText: "取消",
              onOk: () => {
              },
              onCancel: () => {
              }
          });
          break;
      }
    },
    saveDatas(data){
      this.showAddModal = false;
      this.axios
      .get("/model/orgContrModel/getOrgContrModel")
      .then(response => {
        if (response.code == "200") {
          this.tableBody = [];
          this.tableBody.push(response.data.orgEvalContrModel);
          this.tableBody2 = response.data.orgEvalContrIndicator;
        }
      })
      .catch(error => {});
    },
    cancelDialog(val) {
      this.showAddModal = val;
    },
  },
  watch: {
  }
};
</script>
<style lang="scss" scoped>
.ration-model-set {
  .mt_20{
    margin-top: 20px;
  }
}
</style>


