<template>
  <div class="history-version">
    <div class="contentCellTitle">
      <div class="contentCellTitle-1"></div>发布历史模型
    </div>
    <div class="contentCellBody">
      <Row>
        <table class="tableBox">
          <thead>
            <tr>
              <td v-for="(item, index) in tableHeader" :key="index">{{item.name}}</td>
            </tr>
          </thead>
          <tbody>
            <template v-for="(item,index) in tableBody">
              <tr :class="item.version==version?'bgColor':''" :key="index" @click="updateTableBody2(item)">
                <td>{{index+1}}</td>
                <td>{{item.modelCode}}</td>
                <td>{{item.modelName}}</td>
                <td>{{item.version}}</td>
                <td>{{item.scope}}</td>
                <td>
                  <i-switch v-model="item.flag" true-value="1" false-value="0" disabled>
                    <span slot="open">启用</span>
                    <span slot="close">停用</span>
                  </i-switch>
                </td>
                <td>{{item.remark}}</td>
                <td>{{item.updateTime}}</td>
                <td>{{item.updateName}}</td>
                <td>
                  <span class="spanBox" @click="operate('2', item)">重置为本版本</span>
                  <!-- <span class="spanBox" @click="operate('3', item)">删除</span> -->
                </td>
              </tr>
            </template>
          </tbody>
        </table>
        <table class="tableBox mt_20">
          <thead>
            <tr>
              <td v-for="(item,index) in tableHeader2" :key="index">{{item.name}}</td>
            </tr>
          </thead>
          <tbody>
            <template v-for="(item) in tableBody2">
              <tr>
                <td>{{item.indicatorCode}}</td>
                <td>
                  <el-popover
                    placement="top-start"
                    title
                    width="200"
                    trigger="hover"
                    :content="item.indicatorName"
                  >
                    <p slot="reference" class="textOverflow textMaxWidth">{{item.indicatorName}}</p>
                  </el-popover>
                </td>
                <td>{{item.formula}}</td>
                <td>{{item.weight}}</td>
                <td>
                    <i-switch v-model="item.flag" true-value="1" false-value="0" disabled>
                        <span slot="open">启用</span>
                        <span slot="close">停用</span>
                    </i-switch>
                </td>
                <td>{{item.updateTime}}</td>
                <td>{{item.updateName}}</td>
                <td>
                  <span class="spanBox" @click="operate('5', item)" v-if="item.hasSon == '0'">详情</span>
                </td>
              </tr>
            </template>
          </tbody>
        </table>
      </Row>
    </div>
    <Modal :mask-closable="false"  
      v-model="showAddModal"
      :title="modelTitle"
      @on-cancel="cancel"
      class="dialog"
      :styles="{width: '80%'}"
    >
      <component
        :is="tabview"
        @cancel="cancelDialog"
        v-if="showAddModal"
        @save="saveDatas"
        :dataObj="dataObjj"
        width="80%"
      ></component>
      <div slot="footer"></div>
    </Modal>
  </div>
</template>
<script>
//table2部分
import tab2Analyze from "../rationModelSet2/tab2Analyze"; //分析
import tab2Detail from "../rationModelSet2/tab2Detail"; //详情
export default {
  props: ["dataObj"],
  created(){
      this.selectTable1Url = this.dataObj.typeNm == "固有"?"/model/orgEvalModel/getModelHisInfo/":"/model/orgContrModel/getContrModelHisInfo/";
      this.selectTable2Url = this.dataObj.typeNm == "固有"?"/model/orgEvalModel/getIndHisInfo/":"/model/orgContrModel/getContrIndHisInfo/"
      this.axios
          .get(this.selectTable1Url+this.dataObj.data.id)
          .then(response => {
            if (response.code == "200") {
                this.tableBody = response.data;
                this.version = this.tableBody[0].version;
                this.axios
                .get(this.selectTable2Url+this.tableBody[0].modelId+"/"+this.tableBody[0].version)
                .then(response => {
                  if (response.code == "200") {
                      this.tableBody2 = response.data;           
                  }
                })
                .catch(error => {});           
            }
          })
          .catch(error => {});
  },
  components: {
    tab2Analyze,
    tab2Detail,
  },
  data() {
    return {
      showAddModal: false,
      modelTitle: "详情",
      version: "",
      selectTable1Url: "",
      selectTable2Url: "",
      dataObjj: {
        typeNm: "",
        data: {}
      },
      tabview: "",
      tableHeader: [
        { name: "序" },
        { name: "模型代码" },
        { name: "模型名称" },
        { name: "模型版本" },
        { name: "适用范围" },
        { name: "状态" },
        { name: "备注" },
        { name: "更新时间" },
        { name: "更新人员" },
        { name: "操作" }
      ],
      tableBody: [
      ],
      tableHeader2: [
        { name: "编号" },
        { name: "指标项" },
        { name: "计算公式" },
        { name: "权重（%）" },
        { name: "状态" },
        { name: "更新时间" },
        { name: "更新人员" },
        { name: "操作" }
      ],
      tableBody2: [
      ]
    };
  },
  methods: {
    shhowHistory() {
      this.showAddModal = true;
    },
    cancel() {},
    saveDatas(data) {
      this.showAddModal = false;
      this.tableBody[this.dataIndex] = data;
    },
    cancelDialog(val) {
      this.showAddModal = val;
    },
    updateTableBody2(item){
      this.version = item.version;
      this.axios
      .get(this.selectTable2Url+item.modelId+"/"+item.version)
      .then(response => {
        if (response.code == "200") {
            this.tableBody2 = response.data;           
        }
      })
      .catch(error => {}); 
    },
    operate(type, item) {
      switch (type) {
        case "2":
          let url = this.dataObj.typeNm == "固有"?`/model/orgEvalModel/publishModelOld/${item.id}`:`/model/orgContrModel/publishModelOld/${item.id}`
            this.$Modal.confirm({
              title: "提示",
              content: "重置为本版本后，目前使用的版本将成为历史！",
              okText: "确认",
              cancelText: "取消",
              onOk: () => {
                  this.axios
                  .get(url)
                  .then(response => {
                      if (response.code == "200") {
                        this.$Message.success("操作成功");
                        this.$emit("save");
                      }
                  })
                  .catch(error => {});
              },
              onCancel: () => {}
            });
          break;
        case "3":
          this.$Modal.confirm({
            title: "提示",
            content: "是否删除？",
            okText: "确认",
            cancelText: "取消",
            onOk: () => {},
            onCancel: () => {}
          });
          break;
        case "5":
          this.dataObjj = {
            typeNm:this.dataObj.typeNm,
            data:item,
            noOpe: true
          };
          this.modelTitle = "模型因子";
          this.tabview = "tab2Detail";
          this.showAddModal = true;
          break;
      }
    }
  }
};
</script>
<style lang="scss">
.history-version {
  .tab-title {
    font-size: 14px;
    color: #333;
    font-weight: bold;
  }
  table {
    width: 100%;
    font-size: 14px;
    color: #333333;
    margin-bottom: 20px;
    margin-top: 0;
    .show-detail {
      color: #0000cc;
      cursor: pointer;
      text-align: center;
    }
    &.first-tab {
      td {
        border: 0.5px solid #ebecee;
        height: 30px;
        line-height: 30px;
        padding-left: 20px;
        &:nth-child(odd) {
          font-weight: bold;
        }
      }
    }
    &.second-tab {
      thead {
        tr {
          td {
            font-weight: bold;
          }
        }
      }
      tbody {
        td {
          &:nth-child(odd) {
            font-weight: normal;
          }
        }
      }
    }
  }
  .bgColor{
    background: #DEF1FF;
  }
}
</style>


